import React, { useState, useEffect, useCallback } from "react";
import { Panel, PanelHeader, FixedLayout, Div } from "@vkontakte/vkui";
import Crown from "../resources/Crown.png";

import Lobby from "./Lobby";
import Timer from "../components/Timer";
import Results from "./Results";

const Home = ({ id, go, tn, t1, t2, t3, t4 }) => {
  var myArray = [
    "Яблоко",
    "Жираф",
    "Акробат",
    "Телевизор",
    "Блюдце",
    "Сердце",
    "Фонарик",
    "Вешалка",
  ];
  const [points1, setPoints1] = useState(0);
  const [points2, setPoints2] = useState(0);
  const [points3, setPoints3] = useState(0);
  const [points4, setPoints4] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(60);
  const [word, setWord] = useState(
    myArray[Math.floor(Math.random() * myArray.length)]
  );
  const [round, setRound] = useState(1);
  const [roundT, setRoundT] = useState(1);
  const [game, setGame] = useState(false);
  const [results, setResults] = useState(false);

  if (results === false) {
    return (
      <Panel separator={false} id={id}>
        <FixedLayout vertical="top">
          <img className="Game-logo" src={Crown} alt="crown_icon" />
          <text className="Game-txt1">Отгадывает команда:</text>
          <text className="Game-txt2">
            {roundT === 1 ? t1 : null}
            {roundT === 2 ? t2 : null}
            {roundT === 3 ? t3 : null}
            {roundT === 4 ? t4 : null}
          </text>
          <text className="Game-txt3">Раунд {Math.ceil(round / tn)}</text>
        </FixedLayout>

        <Div id={id} className="Main--bg">
          <button
            onClick={() => {
              setGame(true);
              setWord(myArray[Math.floor(Math.random() * myArray.length)]);
            }}
            className={game ? "hidden" : "Main-button"}
          >
            НАЧАТЬ
          </button>
          <text className={game ? "Game-word" : "hidden"}>{word}</text>
        </Div>
        <FixedLayout vertical="bottom">
          <text className={game ? "Game-txt5" : "hidden"}> Отгадали?</text>
          <Div className="Game-line">
            <button
              onClick={() => {
                setWord(myArray[Math.floor(Math.random() * myArray.length)]);
              }}
              className={game ? "Game-btn" : "hidden"}
            >
              Нет
            </button>
            <button
              onClick={() => {
                setWord(myArray[Math.floor(Math.random() * myArray.length)]);
                setPoints1(roundT === 1 ? points1 + 1 : points1);
                setPoints2(roundT === 2 ? points2 + 1 : points2);
                setPoints3(roundT === 3 ? points3 + 1 : points3);
                setPoints4(roundT === 4 ? points4 + 1 : points4);
              }}
              className={game ? "Game-btn" : "hidden"}
            >
              Да
            </button>
          </Div>

          <text className={game ? "hidden" : "Game-txt4"}>
            {" "}
            Нажимайте “ДА”, если ваша команда отгадала слово и “НЕТ” чтобы
            пропустить слово, если его не получается отгадать
          </text>
        </FixedLayout>
      </Panel>
    );
  } else {
    return (
      <Results
        tn={tn}
        t1={t1}
        t2={t2}
        t3={t3}
        t4={t4}
        p1={points1}
        p2={points2}
        p3={points3}
        p4={points4}
        round={round}
      />
    );
  }
};

export default Home;
