import React, { useState, useEffect, useCallback } from "react";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import FixedLayout from "@vkontakte/vkui/dist/components/FixedLayout/FixedLayout";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Girl from "../resources/Girl.png";
import { Input, FormLayout, FormLayoutGroup } from "@vkontakte/vkui";

import Game from "./Game";

const Home = ({ id, go }) => {
  const [teams, setTeams] = useState(2);
  const [team1, setTeam1] = useState("");
  const [team2, setTeam2] = useState("");
  const [team3, setTeam3] = useState("");
  const [team4, setTeam4] = useState("");

  const [ready, setReady] = useState(false);

  if (ready == false) {
    return (
      <Panel separator={false} id={id}>
        <PanelHeader transparent={true}></PanelHeader>
        <FixedLayout vertical="top">
          <text className="Lobby-txt">
            Разделитесь на команды и выберите название для каждой из {teams}{" "}
            команд:
          </text>
          <img className="Lobby-girl" src={Girl} alt="crown_icon" />
          <text className="Lobby-txt2">Команды:</text>
          <Input
            type="text"
            placeholder="Введите название..."
            maxLength="12"
            onChange={(event) => setTeam1(event.target.value)}
            className="Lobby-input"
          />
          <Input
            type="text"
            placeholder="Введите название..."
            maxLength="12"
            onChange={(event) => setTeam2(event.target.value)}
            className={teams > 1 ? "Lobby-input" : "hidden"}
          />
          <Input
            type="text"
            placeholder="Введите название..."
            maxLength="12"
            onChange={(event) => setTeam3(event.target.value)}
            className={teams > 2 ? "Lobby-input" : "hidden"}
          />
          <Input
            type="text"
            placeholder="Введите название..."
            maxLength="12"
            onChange={(event) => setTeam4(event.target.value)}
            className={teams > 3 ? "Lobby-input" : "hidden"}
          />
          <Div className="Lobby-line">
            <button
              onClick={() =>
                teams < 4 ? setTeams(teams + 1) : setTeams(teams)
              }
              className="Lobby-plus"
            >
              Добавить
            </button>
            <button
              onClick={() => setTeams(teams - 1)}
              className={teams > 2 ? "Lobby-plus" : "hidden"}
            >
              Удалить
            </button>
          </Div>
        </FixedLayout>

        <Div id={id} className="Main--bg"></Div>
        <FixedLayout vertical="bottom">
          <button
            onClick={() => setReady(true)}
            data-to="game"
            className="Lobby-button2"
          >
            Продолжить
          </button>
        </FixedLayout>
      </Panel>
    );
  } else {
    return <Game tn={teams} t1={team1} t2={team2} t3={team3} t4={team4} />;
  }
};

export default Home;
